@import '../../../../misc/colors.module.scss';

.asset-selector {
  position: absolute;

  padding: 1em;

  width: 100%;
  height: 100%;

  top: 0;
  left: 0;

  background-color: $gray2;

  z-index: 2;

  font-weight: bold;

  &__heading {
    font-size: 1.5em;
    padding: 1rem 0;
  }

  &__asset-item {
    cursor: pointer;
    padding: 0.5em 0;

    &:hover {
      color: $green1;
    }
  }
}
